/***************************
HOMEPAGE WHEN USER IS NOT LOGGED IN
***************************/
.home--general--main {
  margin-top: 4.58em;
  background-color: var(--home-logged-bg);
  width: 100%;
  display: grid;
  grid-template-columns: 1fr;
  padding: 0 1.5em 3em;
}
.home__svg__container {
  width: 100%;
}
.home__svg__container img {
  display: block;
  width: 100%;
}
.home__content__container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-family: "Poppins", sans-serif;
}
.home__content__container h1 {
  font-family: "Nunito", cursive;
  align-self: flex-start;
  margin-bottom: 0.35em;
  letter-spacing: 0.06em;
  text-transform: uppercase;
  font-size: 2.3em;
  color: var(--h1-color);
}
.home__content__container p {
  color: var(--nav-heading-color);
  font-size: 1em;
}
.home__content__container p a {
  display: inline-block;
  color: var(--general-color);
  text-decoration: none;
  font-weight: 600;
}
.home__content__container p a:hover, .home__content__container p a:focus {
  text-decoration: 2px underline currentColor;
  text-underline-offset: 0.15em;
}

.login__link {
  align-self: flex-start;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  padding: 0.75em 1.5em 0.75em 2.5em;
  text-decoration: none;
  background-image: linear-gradient(to top, var(--btn-color-1), var(--btn-color-2));
  margin-top: 2em;
  text-transform: uppercase;
  color: var(--color-white);
  border-radius: 0.25em;
  box-shadow: 0 0 4px var(--btn-shadow-color);
}
.login__link i {
  margin-left: 1em;
}
.login__link:hover, .login__link:focus {
  --btn-color-1: var(--btn-hover-color-1);
  --btn-color-2: var(--btn-hover-color-2);
}

/******************************
HOMEPAGE WHEN USER IS LOGGED IN
*******************************/
.create__post__link__container {
  position: fixed;
  bottom: 15px;
  right: 20px;
}
.create__post__link__container a {
  text-decoration: none;
  background-color: var(--general-color);
  padding: 0.85em 1.1em;
  border-radius: 50%;
  display: block;
  box-shadow: 0 4px 10px 2px rgba(0, 0, 0, 0.5);
  transition: transform 150ms linear;
}
.create__post__link__container a i {
  color: var(--color-white);
  font-size: 1.45em;
}
.create__post__link__container a:hover, .create__post__link__container a:focus {
  transform: scale(0.96);
}

.home__logged--main {
  width: 100%;
  margin-top: 4.6em;
  padding: 3em 2em 1.5em;
  font-family: "Poppins", sans-serif;
}
.home__logged__posts__container {
  max-width: 540px;
  margin: 0 auto 2em;
  display: grid;
  grid-template-columns: 1fr;
  grid-row-gap: 2em;
}
.home__logged__posts__container article {
  width: 100%;
  background-color: var(--color-white);
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.15);
  border-radius: 0.5em;
  display: grid;
  grid-template-columns: 1fr;
}
.home__logged__posts__container article:hover {
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.35);
}

.post {
  /***************************
  POST AUTHOR RELATED STYLING
  ***************************/
  /****************************
  POST BUTTONS RELATED STYLING
  *****************************/
}
.post__header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-direction: row;
  padding: 1em;
}
.post__author__container {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: row;
}
.post__author__img__wrapper a {
  text-decoration: none;
  display: block;
  margin-right: 0.5em;
  width: 3em;
  height: 3em;
  border-radius: 50%;
}
.post__author__img__wrapper img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
  border-radius: 50%;
  border: 2px solid var(--general-color);
}
.post__author__name__container div {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: row;
}
.post__author__name__container div a {
  text-decoration: none;
  display: block;
}
.post__author__name__container .author__name {
  font-size: 0.9em;
  color: var(--h1-color);
  line-height: 1.3;
}
.post__author__name__container .author__name:hover, .post__author__name__container .author__name:focus {
  opacity: 0.65;
}
.post__author__name__container .current__user__label {
  display: block;
  margin-left: 1em;
  padding: 1px 8px;
  border-radius: 3px;
  color: var(--color-white);
  font-size: 0.7em;
  background-color: #7b19dd;
}
.post__author__name__container span {
  line-height: 1.2;
  font-size: 0.85em;
  display: block;
  color: #494c59;
  font-family: "Ubuntu", sans-serif;
}
.post__related__btn__links__container {
  position: relative;
}
.post__related__btn__links__container .sideNavShowBtn,
.post__related__btn__links__container .sideNavHideBtn {
  background: transparent;
  border: none;
  cursor: pointer;
}
.post__related__btn__links__container .sideNavShowBtn i,
.post__related__btn__links__container .sideNavHideBtn i {
  padding: 0.15em;
}
.post__related__btn__links__container div {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  position: absolute;
  top: -1em;
  right: -1em;
  width: 8em;
  padding: 2em 0.75em 1em;
  display: none;
  background-color: var(--color-white);
  border-radius: 0.3em;
  box-shadow: -4px 4px 6px rgba(0, 0, 0, 0.3);
}
.post__related__btn__links__container div a {
  font-size: 0.9em;
  text-decoration: none;
  display: block;
  color: var(--general-color);
}
.post__related__btn__links__container div a i {
  margin-right: 0.2em;
}
.post__related__btn__links__container div a:hover, .post__related__btn__links__container div a:focus {
  opacity: 0.7;
  cursor: pointer;
}
.post__related__btn__links__container div > a {
  margin-bottom: 0.25em;
}
.post__related__btn__links__container div .sideNavHideBtn {
  position: absolute;
  top: 0.75em;
  right: 1em;
}

/***************************
POST ASSETS
***************************/
.post__content__container {
  padding: 0 1em;
}
.post__content__container a {
  text-decoration: none;
  display: block;
  color: var(--label-color);
  font-family: "Noto Sans", sans-serif;
  line-height: 1.5;
  font-size: 1em;
  margin-bottom: 0.5em;
}
.post__image__container a {
  text-decoration: none;
  width: 100%;
  display: block;
}
.post__image__container img {
  width: 100%;
  display: block;
}

/************************
LIKE AND COMMENT BTNS
*************************/
.post__footer {
  border-top: 1px solid var(--input-border-color);
  padding: 0.25em;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: row;
}

.heart__btn,
.comment__btn {
  text-decoration: none;
  display: block;
  color: #3f3d4f;
  border: none;
  background-color: transparent;
  font-size: 1.5em;
  cursor: pointer;
}
.heart__btn span,
.comment__btn span {
  font-size: 0.85em;
  font-family: "Noto Sans", sans-serif;
}

.heart__btn__active {
  color: var(--grad-color-1);
}

/*************************
MEDIA QUERIES
**************************/
@media (min-width: 769px) {
  .home--general--main {
    grid-template-columns: repeat(2, 1fr);
    padding: 0 2em 1.5em;
  }
}